import Time from './Time'
import routerGo from './routerGo'
import userInfo from './userInfo'
import {api} from '../util/util'
import nodeUrl from '../util/url'
import components from './components'
import ver from './ver'
import area from '../data/area'
//import city from './city'
import city_A_Z from '../data/city_A_Z'
import dydRouter from '../publicRouter/router'
import export_excel from './excel/export'

function fun(Vue,options) {
    Vue.use(components)

    Vue.prototype.$Time=function(date){ //
        return new Time(date)
    }
    Vue.prototype.$routerGo=routerGo  //

    Vue.prototype.$center=new Vue;//

    Vue.prototype.$userInfo=userInfo;//

    Vue.prototype.$api=api;//

    Vue.prototype.$nodeUrl=nodeUrl//

    Vue.prototype.$JSP=function (data) {
        return JSON.parse(JSON.stringify(data))
    }    //  

    Vue.prototype.$ver=ver//element 

    Vue.prototype.$area=area//>>>

    //Vue.prototype.$city=city//

    Vue.prototype.$city_A_Z=city_A_Z//A_Z

    Vue.prototype.$exImg=nodeUrl+'getImg?imagePath='//

    Vue.prototype.$dydRouter=dydRouter  //

    Vue.prototype.$export_excel=export_excel  //excel
}
export default fun
