import axios from 'axios'
import showMessage  from './message.js'  //
// import encrypt from './crypto.js'
/**
 * @param C 
 * */
let C
let Service = axios.create({
    timeout: 7000, // 
    headers: {
        'Content-Type': 'application/json;charset=UTF-8'
    }
})
/**
 * 
 */
const RC = {
    SUCCESS: "succ",
    FAILURE: "fail",
    NOSESSION: "no session",
    AJAXERROR: "ajax error"
};


/**
 *  
 *
 * @param data 
 * @param url 
 * @param callback 
 * @param method  
 */
let utilServe=function(data,url,callback,method) {
    Service({
        url:url,
        method: method,
        data:data,  //  toFormData(cnt)
    })
        .then(()=>{callback(C)})
        .catch(function (readyState, status, error) {
            callback({
                rc: RC.AJAXERROR,
                rm: (readyState + "&" + status + "&" + error)
            })
        })
}
// 
Service.interceptors.request.use(config => {
    // loadingInstance = Loading.service({
    //     lock: true,
    //     text: 'loading...'
    // })
    return config
})
// 
Service.interceptors.response.use(response => {
    //loadingInstance.close()
    C= response.data
}, error => {
    console.log('TCL: error', error)
    const msg = error.Message !== undefined ? error.Message : ''
    showMessage("",3000,Date.now())
    C=false
})


//data
function toFormData(obj) {//
    let formData = new FormData();
    for(let key in obj) {
        formData.append(key, encrypt(obj[key]));
    }
    return formData;
}



/**
 * @param serveUrl 
 * */

let serveUrl
export default class dydUtil{
    constructor(serve){
        serveUrl=serve //
    }
    util(data={}, url, callback, method='post'){
        data.$db='mysql'
        utilServe(data,serveUrl+url+'?fun='+data.$method+'-'+data.$table,callback,method)
    }
}