export default class GetTime {
    constructor(time){
        this.date=new Date(parseInt(time)) //timestring
        this.Y=this.date.getFullYear()  //
        this.M=this.date.getMonth()+1   //
        this.D=this.date.getDate()      //
        this.h=this.date.getHours()     //
        this.m=this.date.getMinutes()   //
        this.s=this.date.getSeconds()   //
        this.w=this.date.getDay()   //
        this.timeType={
            'Y': this.Y,
            'M': this.M,
            'D': this.D,
            'h': this.h,
            'm': this.m,
            's': this.s,
            'w': this.w,
        }
    }
    getTime(type='Y-M-D h:m:s'){
        const array=type.split('')
        let time=''
        array.forEach((val,index,arr)=>{
            time+=this.timeType[val]===undefined?val:this.timeType[val]
        })
        return time
    }
};