const { export_json_to_excel } =  require('./Export2Excel'); //@/+export2Excel

export default function export2Excel(lists,excelName) {
    require.ensure([], () => {
        let listHeader=[]  //
        let listVal=[]
        let header=lists[0]
        for(let i in header){
            listHeader.push(i)
            listVal.push(i)
        }
        const listData = formatJson(listVal, lists);
        console.log(listData)
        export_json_to_excel(listHeader, listData, excelName);// 
    })
}
//
function formatJson(filterVal, jsonData) {
    return jsonData.map(v => filterVal.map(j => v[j]))
}
